<template>
    <div class="page">
        <van-nav-bar
            :title="$route.name"
        />
        <!-- 商品列表 -->
         <van-row class="goodslist" v-for="(item,index) in lists" :key="index">
            <van-col span="2">
                <van-checkbox  :value="item.isSelect" @click="inputselect(index)"></van-checkbox>
                <!-- <input type="checkbox" :checked="item.isSelect" @click="inputselect(index)" > -->
            </van-col>
            <van-col span="7">
                <van-image
                height="134"
                fit="cover"
                src="https://img.yzcdn.cn/vant/ipad.jpeg"
                />
            </van-col>
                <!-- :src="requrie('../assets/img/goodslist.jpg')" -->
            <van-col span="15">
                <div class="content">
                    <h1>{{item.name}}</h1>
                    <h2>副标题</h2>
                    <p class="price">￥{{item.price}}</p>
                    <p>
                        <van-button type="default" size="mini" @click="item.num <= 1? 1:item.num--">-</van-button>
                        <span>{{item.num}}</span>
                        <van-button type="default" size="mini" @click="item.num++">+</van-button>
                        <van-button type="danger" class="danger" size="mini" @click="del(index)">删除</van-button>
                    </p>
                </div>
            </van-col>
        </van-row>
        <!-- 购物车没有商品时的界面 -->
        <div v-show="!($store.state.cart.lists.length)" >
            <p class="goodsinfo">没有商品，请添加商品。。。</p>
            <div class="goodslisttitle">
                猜你喜欢
            </div>
            <!-- 推荐商品 -->
            <router-link to="/details" tag="a">
                <van-card
                num="2"
                price="3999.00"
                desc="全新华为手机"
                title="华为P40"
                thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
                >
                </van-card>
            </router-link>
            <router-link to="/details" tag="a">
                <van-card
                num="2"
                price="4999.00"
                desc="全新OPPO手机"
                title="OPPO Reno3 Pro"
                thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
                >
                </van-card>
            </router-link>
            <!-- 推荐商品结束 -->
        </div>
        <van-submit-bar :price="total * 100" button-text="提交订单" @submit="$router.push('/pay')" >
            <van-checkbox @click="checkedAll" :value="checked">全选</van-checkbox>
        </van-submit-bar>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                checked:true,
                total:0,
                lists:[
                    // 数据
                ]
                    }
                
            },
        methods:{
            del(index){
                this.lists.splice(index,1)
            },
            sum(){
                // 总价格计算
                // total 累加值
                this.total = this.lists.reduce((total,item)=>{
                    // 判断选项选中计算价格
                    if(!item.isSelect) return total; //返回默认价格
                    return total + parseInt(item.price) * parseInt(item.num); //总价格
                },0) //总价格默认值0
            },
            checkedAll(){
                this.lists.forEach((item)=>{
                    item.isSelect = !this.checked;
                })
                this.checked = !this.checked;
            },
            checkboxclick(){
                // 第一次加载页面过程中，判断复选框状态，全部true，返回true，有一个false,放回false
                this.checked = this.lists.every((item)=>{
                    return item.isSelect;
                })
            },
            inputselect(index){
                // 修改商品列表
                this.lists[index].isSelect = !this.lists[index].isSelect;
                this.checkboxclick();
                this.sum();
            },
            
        },
        
        created(){
            this.lists = this.$store.state.cart.lists;
            this.checkboxclick();
            this.sum();
        },
        beforeUpdate(){
            this.sum();
        },
        beforeDestroy(){
            this.$store.commit("cart/edit",this.lists);
        }
    }
</script>

<style lang="scss" scoped>
.goodslist{
    position: relative;
    background-color: #fff;
    .van-checkbox{
        position: relative;
        left:5px;
        top:37px;
    }
    .content{
        padding-left:10px;
        h1{
            font-size: 16px;
        }
        h2{
            font-size: 12px;
            font-weight: normal;
        }
        .price{
            font-size: 14px;
            color:#323233;
        }
        span{
            display: inline-block;
            width: 30px;
            font-size: 14px;
            text-align: center;
        }
        .danger{
            span{
                font-size: 12px;
            }
        }
    }
}

.van-submit-bar{
    bottom: 50px;
}
.page{
    margin-bottom: 50px;
}
</style>